import { Box, Button, Chip, Container, InputAdornment, Menu, MenuItem, Stack, TextField, Typography } from '@mui/material';
import { Helmet } from 'react-helmet-async';
import { useSettingsContext } from 'src/components/settings';
import { color, decryptToken, formatDate, formatObj } from 'src/utils/common';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import { useState, useEffect } from 'react';
import { constants } from 'src/utils/constant';
import axios, { endpoints } from 'src/utils/axios';
import { useSnackbar } from 'src/components/snackbar';
import DataTable from 'react-data-table-component';
import SearchIcon from '@mui/icons-material/Search';
import { DatePicker, LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import dayjs from 'dayjs';
import * as XLSX from 'xlsx';
import JsPDF from 'jspdf';
import 'jspdf-autotable';
import { saveAs } from 'file-saver';
// sections

// ----------------------------------------------------------------------

export default function PurchaseReturnReport() {
    const settings = useSettingsContext();
    const [isLoading, setIsLoading] = useState(false);
    const [tempList, setTempList] = useState([])
    const [list, setList] = useState([]);
    const [tempData, setTempData] = useState([]);
    const [searchQuery, setSearchQuery] = useState('');
    const { enqueueSnackbar } = useSnackbar();
    // For Actions Dropdown
    const [anchorEl, setAnchorEl] = useState(null);
    const openMenu = Boolean(anchorEl);

    const fetchData = async () => {

        try {
            const decryptedToken = localStorage.getItem(constants.keyUserToken);
            const accessToken = decryptToken(decryptedToken);
            const response = await axios.post(endpoints.purchaseReturn.list, {}, {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                }
            });
            if (response) {
                setIsLoading(false);
                setList(response.data.data);
                setTempData(response.data.data);
                setTempList(response.data.data);
            }
        } catch (err) {
            enqueueSnackbar('Failed to fetch purchase orders!', { variant: 'error' });
            setIsLoading(false);
        }
    };

    useEffect(() => {
        setIsLoading(true);
        fetchData();

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const [startDate, setStartDate] = useState(dayjs().startOf('month'));
    const [endDate, setEndDate] = useState(dayjs().endOf('month'));

    const handleStartDateChange = (date) => {
        setStartDate(date);
    };

    const handleEndDateChange = (date) => {
        setEndDate(date);
    };

    useEffect(() => {
        if (startDate && endDate) {
            const filteredSales = tempList.filter((sale) => {
                const saleDate = dayjs(sale.createdAt);
                console.log(startDate, endDate)
                return saleDate.isBetween(startDate, endDate, null, '[]');
            });
            setList(filteredSales);
        } else {
            setList(tempList);
        };
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [startDate, endDate])

    const columns = [
        {
            name: 'Vendor Name',
            selector: row => row.vendorId.name,
            sortable: 'true',
        },
        {
            name: 'Invoice Number',
            selector: row => row.invoiceNumber,
        },
        {
            name: 'Amount',
            selector: row => row.totalAmount.toFixed(2),
            sortable: 'true',
        },
        {
            name: 'Payment Mode',
            selector: row => row.payments[0].paymentID.paymentMode,
            sortable: 'true',
        },
        {
            name: 'Invoice Date',
            selector: row => new Date(row.createdAt).toLocaleDateString('en-GB', { day: '2-digit', month: 'long', year: 'numeric' }),
            sortable: 'true'
        },
    ];

    const handleDownloadPDF = () => {
        setAnchorEl(null);
        const doc = new JsPDF();
        doc.text('Purchase Return Report', 10, 10);

        // Get table data
        // Transform data and create tableData array
        const tableData = filtered.map(item => [
            item.vendorId.name || 'N/A',
            item.invoiceNumber || 'N/A',
            item.totalAmount.toFixed(2) || 'N/A',
            item.payments[item.payments.length - 1]?.paymentID?.paymentMode || 'N/A',
            item.createdAt ? formatDate(item.createdAt) : 'N/A'
        ]);

        // Set column headers
        const headers = ['Vendor Name', 'Invoice Number', 'Amount', 'Payment Mode', 'Time'];

        // Add table to PDF
        doc.autoTable({
            head: [headers],
            body: tableData,
        });

        doc.save('purchase_return_report.pdf');
    };



    const handleDownloadExcel = () => {
        setAnchorEl(null);
        // Convert data to a format suitable for Excel
        const excelData = filtered.map(item => ({
            'Vendor Name': item.vendorId.name,
            'Invoice Number': item.invoiceNumber,
            'Amount': item.totalAmount.toFixed(2),
            'Payment Mode': item.payments[item.payments.length - 1]?.paymentID?.paymentMode || 'N/A',
            'Invoice Date': item.createdAt ? formatDate(item.createdAt) : 'N/A'
        }));

        // Create a new workbook
        const workbook = XLSX.utils.book_new();
        // Convert the data to a worksheet
        const worksheet = XLSX.utils.json_to_sheet(excelData);
        // Add the worksheet to the workbook
        XLSX.utils.book_append_sheet(workbook, worksheet, 'Purchase Return Report');

        // Convert the workbook to a binary Excel file
        const excelBuffer = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
        // Create a Blob from the buffer
        const data = new Blob([excelBuffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8' });
        // Save the Blob as a file using file-saver
        saveAs(data, 'purchase_return_report.xlsx');
    };

    const filtered = list.filter((data) =>
        data?.vendorId.name?.toLowerCase().includes(searchQuery?.toLowerCase())
    );

    return (
        <Container maxWidth={settings.themeStretch ? false : 'xl'}>
            <Helmet>
                <title> Reports: Purchase Return Report</title>
            </Helmet>

            <Typography variant="h4"> Purchase Return Report </Typography>
            <div style={{ display: 'flex', flexDirection: 'row', justifyContent: "space-between", marginTop: "20px" }}>
                <Stack direction="row" spacing={1}>
                    <Chip
                        label={`Total Amount : ${list.length > 0 ? formatObj.format(list.reduce((accumulator, currentValue) => accumulator + currentValue.totalAmount, 0)) : formatObj.format(0)}`}
                        variant="outlined"
                    />
                </Stack>

                <Stack>
                    <Button style={{ color: color.actionButton }} endIcon={<ArrowDropDownIcon />} variant='outlined' onClick={(e) => setAnchorEl(e.currentTarget)}>
                        ACTIONS
                    </Button>
                    <Menu
                        id="basic-menu"
                        anchorEl={anchorEl}
                        open={openMenu}
                        onClose={() => {
                            setAnchorEl(null);
                            setTempData({});
                        }}
                        MenuListProps={{
                            'aria-labelledby': 'basic-button',
                        }}
                    >

                        <MenuItem onClick={() => {
                        }}>Print</MenuItem>

                        <MenuItem onClick={handleDownloadPDF}>Download as PDF</MenuItem>
                        <MenuItem onClick={handleDownloadExcel}>Download as Excel</MenuItem>

                    </Menu>
                </Stack>
            </div>
            <div style={{ display: "flex", marginTop: "8px", gap: '10px' }}>
                <TextField
                    placeholder='Search'
                    size='small'
                    value={searchQuery}
                    onChange={e => setSearchQuery(e.target.value)}
                    InputProps={{
                        startAdornment: (
                            <InputAdornment position="start">
                                <SearchIcon />
                            </InputAdornment>
                        ),
                    }}
                />
                <div>
                    <LocalizationProvider dateAdapter={AdapterDayjs} >
                        <Box sx={{ display: 'flex', flexDirection: 'row', gap: '10px', alignItems: 'center' }}>
                            <DatePicker
                                label="From"
                                format='DD/MM/YYYY'
                                value={startDate}
                                slotProps={{ textField: { size: 'small' } }}
                                onChange={handleStartDateChange}
                                renderInput={(props) => <TextField {...props} />}
                            />
                            <Typography>-</Typography>
                            <DatePicker
                                label="To"
                                format='DD/MM/YYYY'
                                value={endDate}
                                slotProps={{ textField: { size: 'small' } }}
                                onChange={handleEndDateChange}
                                renderInput={(props) => <TextField {...props} />}
                            />
                        </Box>
                    </LocalizationProvider>
                </div>
            </div>
            <div style={{ marginTop: "20px" }}>
                <DataTable
                    columns={columns}
                    data={filtered}
                    pagination
                    progressPending={isLoading}
                />
            </div>
        </Container>
    );
}
